html , body{
  height: 100%;
}

body{
  font-family: Helvetica;
  margin: 0;
  padding: 0;
  background: linear-gradient(180deg, rgba(9, 35, 175,1) 0%, rgba(0,0,0,1)100%);
  color: white;
}


button{
  background-color: #1646a0;
  color: #fff;
  padding: 0 45px;
  border: 2px solid #fff;
  border-radius: 25px;
  height: 50px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.2em;
  cursor: pointer;
  transition: 0.4s
}

button:hover{
  background-color: #0923af;
}


.App{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.start h1{
    font-size: 3.5em;
}

.start p {
    margin-bottom: 2em;
    color: #ecfa00;
}

.game h1{
    font-size: 2.5em;;
}


.points span{
    font-weight: bold;
}


.tip span{
    color:#ecfa00 ;
}

.wordContainer{
    margin: 1.5em;
    padding: 1.5em;
    border:20px solid #ecfa00 ;
    display: flex;
}


.letter, .blankSquare{
    font-size: 70px;
    line-height: 1.5;
    border: 3px solid #000;
    height: 100px;
    width: 100px;
    text-transform: uppercase;
    background-color: #fff;
    color: #000;
    font-weight: bold;
}


.letterContainer p{
    margin-bottom: 1.2em;;
}

.letterContainer form{
    display:flex;
    align-items: center;
    justify-content: center;
}


.letterContainer input{
    height: 50px;
    width: 50px;
    font-size: 2em;
    text-align: center;
    margin-right: 1em;
}
h2 span{
    color: #ecfa00;
    font-size: 1.5em;
}



